import MediaControlCard from "../../components/card"
import workingteam from "../../assets/images/working-team.jpg";

import "./blog.css"

const Blog = () => {


    return (
        <div className="blog-container">
            <div className="bg-img">
                <h1></h1>
            </div>

            <div className="articles container">
                <MediaControlCard image={workingteam} path="/blog/:+/" >
                    <h2 className="card-title"> :     </h2>
                    <div className="card-text">
                                        .                  ,  ,       </div>
                </MediaControlCard>
            </div>

            <div className="articles-wrap container">
                <MediaControlCard path="/blog/:++/"><h3 className="card-title">  :    </h3><div className="card-text">                    .</div></MediaControlCard>
                <MediaControlCard path="/blog/:++/"><h3 className="card-title">     </h3><div className="card-text">   ,    ,         .</div></MediaControlCard>
                <MediaControlCard path="/blog/:+/"><h3 className="card-title">   </h3><div className="card-text">      ,        :   .</div></MediaControlCard>
                <MediaControlCard path="/blog/:++/"><h3 className="card-title" >  :     ?</h3><div className="card-text">    ,  ,        " "       .</div></MediaControlCard>
                <MediaControlCard path="/blog/:+/"><h3 className="card-title">       ?</h3><div className="card-text">               ,       .</div></MediaControlCard>
            </div>
        </div>
    )
}

export default Blog
