import React from 'react'
import { NavLink } from 'react-router-dom';
import useWindowSize from '../windowSize';
const NavbarItems = () => {
  return (
    <div>
      <ul style={{ display: useWindowSize() <= 910 ? "none" : '' }}>
        <li><NavLink to={"/"} className="link"></NavLink></li>
        <li><NavLink to={"/our-solution/"} className="link"> </NavLink></li>
        <li><NavLink to={"/about-us/"} className="link"> </NavLink></li>
        <li><NavLink to={"/blog/"} className="link"></NavLink></li>
        <li><NavLink to={"/career/"} className="link"></NavLink></li>
        <li ><NavLink to={"/contact-us/"} className="link" > </NavLink></li>
        <li><NavLink to={"/app/"} className="link-button"></NavLink></li>
        <li ><NavLink to={"/app/"} className="link-button"></NavLink></li>
      </ul>
    </div>
  )
}

export default NavbarItems