import "./home.css"
import { LogoWhite, Arrow, Chart, Clock, Smartphone, CashFlow, Competition, Corona, Handshake, Refresh, SaveMoney, OneFingerClick, Lock } from "../../assets/icons";
import Deloitte from "../../assets/images/deloitte-digital.png";
import Erech from "../../assets/images/erechlogo.png";
import Oracle from "../../assets/images/oracle.png";
import BDO from "../../assets/images/bdo.png";
import Obelisk from "../../assets/images/obelisk.png";
import PrimeSec from "../../assets/images/prime-sec.png";
import PWC from "../../assets/images/pwc.png";
import Tadmor from "../../assets/images/tadmor.png";
import MediaControlCard from "../../components/card";
import workingteam from "../../assets/images/working-team.jpg";
import interview from "../../assets/images/interview.webp"
import smiling from "../../assets/images/smiling.webp"
import { Button } from "@mui/material";
import ChevronLeft from "@mui/icons-material/ChevronLeft";
import ContactForm from "../../components/contactForm";
import Carousel from "react-elastic-carousel";
import useWindowSize from "../../components/windowSize";
import { useRef } from "react";
import Slider from 'infinite-react-carousel';
import ArrowBackIosNewIcon from '@mui/icons-material/ArrowBackIosNew';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import { NoEncryption } from "@mui/icons-material";
import { NavLink } from "react-router-dom";

const partners = [Obelisk, Oracle,   PrimeSec, Erech, BDO, Deloitte, Tadmor, PWC]
const careerPath = "../career"

const carouselItems = [
    <div className="advantage-item"><div className="icon-group"><CashFlow className="icon-in" /></div><span className="icon-title-ad"> </span></div>,
    <div className="advantage-item"><div className="icon-group"><SaveMoney className="icon-in" /></div><span className="icon-title-ad">   </span></div>,
    <div className="advantage-item"> <div className="icon-group"><Refresh className="icon-in" /></div><span className="icon-title-ad">   </span></div>,
    <div className="advantage-item"><div className="icon-group"><OneFingerClick className="icon-in" /></div><span className="icon-title-ad">   </span></div>,
    <div className="advantage-item"><div className="icon-group"><Corona className="icon-in" /></div><span className="icon-title-ad">   </span></div>,
    <div className="advantage-item"><div className="icon-group"><Handshake className="icon-in" /></div><span className="icon-title-ad">   </span></div>,
    <div className="advantage-item"><div className="icon-group"><Lock className="icon-in" /></div><span className="icon-title-ad"> </span></div>,
    <div className="advantage-item"><div className="icon-group"><Competition className="icon-in" /></div><span className="icon-title-ad"> </span></div>,
]


const Home = () => {


    const settings = {
       autoplay: true,
       autoplaySpeed: 5000,
        slidesPerRow: 2,
        dots: true,
        prevArrow: <div style={{ margin: "0", position: "absolute" }}><ArrowBackIosNewIcon style={{ color: "#fff" }} /></div>,
        nextArrow: <div style={{ margin: "0", position: "absolute" }}><ArrowForwardIosIcon style={{ color: "#fff" }} /></div>,
   
    };

    const settings1 = {
        autoplay: true,
        autoplaySpeed: 5000,
        slidesPerRow: 4,
        dots: true,
        arrows: false, 
    };

    


    return (
        <div className="page-container">
            <div className="home-jumbotron">
                <div className="home-jumbotron-overlay"></div>

                <div className="jumbotron-content">
                    <div>
                        <LogoWhite className="logo-white" />
                    </div>
                    <div>
                        <h1>    </h1>
                        <h1>   </h1>
                    </div>
                    <div className="arrow-link" >
                        <Arrow className="arrow-icon" />
                    </div>
                </div>
            </div>

            <div className="our-solution" >
                <div className="container">
                    <div className="our-solution-content">
                        <h2 className="our-solution-title">. </h2>
                        <p className="our-solution-text" >     ( )          </p>
                    </div>
                    <div className="icons-row">
                        <div className="icon-wrap"><div  className="circle"><Chart className="solution-icon" /></div> <span className="icon-title"> </span></div>
                        <div className="icon-wrap"><div   className="circle"><Clock className="solution-icon" /></div><span className="icon-title">  </span></div>
                        <div className="icon-wrap"><div  className="circle"><Smartphone className="solution-icon" /></div><span className="icon-title">  </span></div>
                    </div>
                </div>
            </div>
            <div className="advantages">
                <div className="advantages-content">
                    <h2 className="advantages-title">. </h2>
                    {useWindowSize() <= 767 ?
                        <div className="carousel-wrapper">
                            <Slider {...settings}>
                                {carouselItems.map(item => <div >{item}</div>)}
                            </Slider>
                        </div> :
                        <div className="icon-square">
                            {carouselItems.map(item => <div>{item}</div>)}
                        </div>}
                </div>
            </div>

            <div className="partners container" >
                <h2 className="partners-title">. </h2>
                {useWindowSize() <= 767 ?
                    <div className="carousel-wrapper">
                        <Slider {...settings1}>
                            {partners.map(item => (<div className="carousel-item-dv" ><img  className="image-carousel" src={`${item}`}/></div>))}

                        </Slider>
                    </div>
                    :
                    <div className="images-row">
                        {partners.map(item => (<div className="image" style={{ backgroundImage: `url(${item})` }}></div>))}
                    </div>}
            </div>


            <div className="articles container">
                <h2 className="articles-title">.</h2>
                <MediaControlCard image={workingteam} path="/blog/:+/">
                    <h2 className="card-title" > :     </h2>
                    <div className="card-text">
                                        .                  ,  ,       </div>
                </MediaControlCard>
            </div>
            <div className="articles-row container">
                <MediaControlCard path="/blog/:++/"><h2 className="card-title">  :    </h2><div className="card-text">                    .</div></MediaControlCard>
                <MediaControlCard path="/blog/:++/"><h2 className="card-title">     </h2><div className="card-text">   ,    ,         .</div></MediaControlCard>
                <MediaControlCard path="/blog/:+/"><h2 className="card-title">   </h2><div className="card-text">      ,        :   .</div></MediaControlCard>
            </div>
            <div className="article-btn">
                <NavLink  to="./blog/" className="articles-btn"><ChevronLeft />  </NavLink>
            </div>

            <div className="career-container container">

                <div className="career-title"><h3>.</h3></div>
                <div className="career-content">
                    <div className="career-photos">
                        <img className="large-photo" src={interview} />
                        <img className="small-photo" src={smiling} />
                    </div >
                    <div className="text-container">
                        <div className="text-wrapper">
                            <div className="career-second-title"><h3 style={{ margin: "0", lineHeight: "1" }}>  .</h3></div>
                            <div className="career-third-title"><p style={{ marginTop: "0", lineHeight: "1" }}>Up Capital   !</p></div>
                            <div className="career-text"><p style={{ marginTop: "0", lineHeight: "1" }}>Up Capital             .</p></div>
                            <div className="career-text"><p style={{ marginTop: "0", lineHeight: "1" }}>      ,  ,    .</p></div>
                            <div className="career-text"><p style={{ marginTop: "0", lineHeight: "1" }}>    ,             .</p></div>
                        </div>
                        <div className="jobs-btn">
                            <Button variant="contained" href={careerPath} style={{ backgroundColor: "#2f439a", padding: "7px 20px", fontFamily: '"Helvetica Hebrew",Sans-serif', fontSize: "20px", fontWeight: "700", width: "auto" }}> <ChevronLeft /></Button>
                        </div>
                    </div>
                </div>
                <div className="job-offers container">
                    <div className="job-title-wrap">
                        <h3 className="job-title">. </h3>
                    </div>
                    <div className="articles-row">
                        <MediaControlCard>
                            <h2 className="card-title">/   </h2>
                            <div className="card-text">       ,         ,    .</div>
                            <div className="job-details-wrap">
                                <div className="job-details"> : 102</div>
                                <div className="job-details"> :  </div>
                                <div className="job-details"> : </div>
                                <div className="job-details">   : 01/12/2021</div>
                            </div>
                        </MediaControlCard>
                        <MediaControlCard>
                            <h2 className="card-title">/  </h2>
                            <div className="card-text">      , , ,  ,   ,   .</div>
                            <div className="job-details-wrap">
                                <div className="job-details"> : 201</div>
                                <div className="job-details"> :  </div>
                                <div className="job-details"> : </div>
                                <div className="job-details">   : 01/11/2021</div>
                            </div>
                        </MediaControlCard>
                        <MediaControlCard>
                            <h2 className="card-title">/ </h2>
                            <div className="card-text">            ,    .</div>
                            <div className="job-details-wrap">
                                <div className="job-details"> : 301</div>
                                <div className="job-details"> :  </div>
                                <div className="job-details"> : </div>
                                <div className="job-details">   : 01/12/2021</div>
                            </div>
                        </MediaControlCard>

                    </div>
                </div>

            </div >
            <div className="contact">
                <div className="contact-wrapper" ><ContactForm /></div>
            </div>
        </div >
    )
};

export default Home;
